
import { reactive, computed } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import { ref } from 'vue'

export default {
  name: 'color-dialog',
  emits: ['edit-color'],
  // emits: ['create-birthday'],
  props: {
    open: {
      type: Boolean,
      default: false
    },
    // info: {
    //   type: Object,
    //   required: true,
    // }
  },
  setup(props, { emit }) {
    // const store = useStore()
    const router = useRouter()
    const state = reactive({
      dialogVisible: computed(() => props.open),
    })
    const value = ref('')

    const handleClose = function () {
      emit('closeColorDialog')
    }

    const options = [
  {
    value: '1',
    label: '()',
  },
  {
    value: '2',
    label: '',
  },
  {
    value: '3',
    label: '',
  },
  {
    value: '4',
    label: '',
  },
  {
    value: '5',
    label: '',
  },
]

  const editColor = function () {
      if ( ref(value).value != '') {
        // emit('edit-color', {
        //   color: ref(value),
        // })
        emit('edit-color', {
          jellyc: ref(value).value,
        })
        console.log(' ')
        console.log(ref(value).value)
        // console.log(props.info.cdate)
        localStorage.setItem('jelly', JSON.stringify(ref(value).value));
        setTimeout(() => {
                swal({
                  title: " ",
                  text: "  ..",
                  icon: "success",
                  button: "",
                });
              }, 500)

              router.go(router.currentRoute)
        handleClose()

      } else {
        setTimeout(() => {
                swal({
                  title: "  ",
                  text: " ",
                  icon: "error",
                  button: "",
                });
              }, 500)

              // router.go(router.currentRoute)
      }
    }

    return { state, handleClose, value,  options, editColor }
  }

}
